import React, { memo } from "react";
import { SportsCategoryMobileWrapper } from "./style";
import KTClassifyHeader from "@/components/classify/classify-header";
import KTListViewHorizontal from "@/components/scroll-view/list-view-horizontal";
import KTSpace from "@/components/space";
import KTSportsCategoryCell from "../sports-category-cell";

const KTSportsCategoryMobile = memo(({ categorys, windowWidth }) => {
  return (
    <SportsCategoryMobileWrapper>
      <KTClassifyHeader
        title="Categories"
        windowWidth={windowWidth}
        hiddenViewAll={true}
      />
      <KTSpace height={windowWidth > 480 ? "30px" : "30rem"} />
      <KTListViewHorizontal
        items={categorys}
        windowWidth={windowWidth}
        spaceBetween={"30rem"}
        swiperPadding={`14rem 20rem`}
        cellOfItem={(item) => {
          return <KTSportsCategoryCell item={item} />;
        }}
      />
    </SportsCategoryMobileWrapper>
  );
});

export default KTSportsCategoryMobile;
