import React, { memo } from "react";
import {
  ShowsCategoryCellIconWrapper,
  ShowsCategoryCellWrapper,
} from "./style";
import { ReactSVG } from "react-svg";
import classNames from "classnames";
const KTShowsCategoryCell = memo(({ item, isSelected = false }) => {
  return (
    <ShowsCategoryCellWrapper>
      <ShowsCategoryCellIconWrapper>
        <ReactSVG
          src={require(`@/assets/img/temporary/${item.icon}`)}
          style={{
            stroke: isSelected ? "#4D4D4D" : "#B3B3B3",
            "stroke-width": "1",
            width: "36rem",
            height: "36rem",
            viewBox: "0 0 36rem 36rem",
          }}
        />
      </ShowsCategoryCellIconWrapper>
      <div
        className={classNames("name", {
          nameActive: isSelected,
        })}
      >
        {item.name}
      </div>
    </ShowsCategoryCellWrapper>
  );
});

export default KTShowsCategoryCell;
